/* eslint-disable import/no-webpack-loader-syntax */
import Text from '../../component/Text'
import styles from './index.less'
import FlexContainer from 'component/FlexContainer'
import FlexItem from 'component/FlexItem'
import classNames from 'classnames'
import Code from 'component/Code'
import lessStyle from '!!raw-loader!./color.less'
const ColorView = () => {


    return (
        <div>
            <section className={styles.section}>

                <Text className={styles.title}>色彩1</Text>
                <Text>UI 定义了PC，Mac客户端内颜色的使用场景和标准，并在组件库中实现</Text>
            </section>
            <section className={styles.section}>
                <Text level="h2" className={styles.title}>主题色</Text>
                <Text>重点突出的信息，指示可交互的控件。</Text>
                <FlexContainer flex="horizontal-warp" className={styles.colorContainer}>
                    <FlexItem className={classNames(styles.commItem, styles.mainColor)}>默认状态/#00B831</FlexItem>
                    <FlexItem className={classNames(styles.commItem, styles.mainHoverColor)}> 鼠标悬浮状态/#00C735</FlexItem>
                    <FlexItem className={classNames(styles.commItem, styles.mainClickColor)}>  鼠标按下状态/#00A82D</FlexItem>
                </FlexContainer>
            </section>

            <section className={styles.section}>
                <Text level="h2" className={styles.title}>辅助色</Text>
                <Text>在多个重点突出信息下，与主题色并列使用。</Text>
                <FlexContainer flex="horizontal-warp" className={styles.colorContainer}>
                    <FlexItem className={classNames(styles.commItem, styles.assistColor)}>  #2F85F9</FlexItem>
                </FlexContainer>
            </section>
            <section className={styles.section}>
                <Text level="h2" className={styles.title}>文字色</Text>
                <Text>注重阅读体验，UI 提供如下文字色彩：</Text>
                <FlexContainer flex="horizontal-warp" className={styles.colorContainer}>
                    <FlexItem className={classNames(styles.commItem, styles.textColor)}>  主文字色/#0f0f0f</FlexItem>
                    <FlexItem className={classNames(styles.commItem, styles.textLinkColor)}>链接文字色/#33508F</FlexItem>
                    <FlexItem className={classNames(styles.commItem, styles.textImportantColor)}> 突出文字色/#4A4F54</FlexItem>
                    <FlexItem className={classNames(styles.commItem, styles.textSubImportantColor)}>  次级突出文字色/#6C737B</FlexItem>
                    <FlexItem className={classNames(styles.commItem, styles.textTipColor)}>
                        辅助文字色/#93959E
                    </FlexItem>
                    <FlexItem className={classNames(styles.commItem, styles.textSubTipColor)}> 次级辅助文字色/#AEAFB5</FlexItem>
                </FlexContainer>
            </section>

            <section className={styles.section}>
                <Text level="h2" className={styles.title}>边框色</Text>
                <Text>使用边框分隔操作或显示区域：</Text>
                <FlexContainer flex="horizontal-warp" className={styles.colorContainer}>
                    <FlexItem className={classNames(styles.commItem, styles.borderColor)}> 主边框/#D7D9DB</FlexItem>
                    <FlexItem className={classNames(styles.commItem, styles.areaDividerColor)}>  区域分隔/#E6E7EB</FlexItem>
                </FlexContainer>
            </section>

            <section className={styles.section}>
                <Text level="h2" className={styles.title}>背景色</Text>
                <Text>使用背景衬托页面主要区域，UI提供已下背景：</Text>
                <FlexContainer flex="horizontal-warp" className={styles.colorContainer}>
                    <FlexItem className={classNames(styles.commItem, styles.backColor)}> 背景主色/#F6G8FA</FlexItem>
                    <FlexItem className={classNames(styles.commItem, styles.backDarkColor)}>  区域 背景深色/#F0F0F5</FlexItem>
                    <FlexItem className={classNames(styles.commItem, styles.backHoverColor)}>   悬停状态/#F3F3F6</FlexItem>
                    <FlexItem className={classNames(styles.commItem, styles.backActiveColor)}>   按下,选中状态/#F3F3F6</FlexItem>
                    <FlexItem className={classNames(styles.commItem, styles.backSelectedColor)}>   文字选中/#CDE2FE</FlexItem>
                </FlexContainer>
            </section>

            <section className={styles.section}>
                <Text level="h2" className={styles.title}>功能色</Text>
                <Text>功能色用来告知客户状态：</Text>
                <FlexContainer flex="horizontal-warp" className={styles.colorContainer}>
                    <FlexItem className={classNames(styles.commItem, styles.warningColor)}> 警告色/#FF8A05</FlexItem>
                    <FlexItem className={classNames(styles.commItem, styles.errorColor)}>   错误色/#FF534E</FlexItem>
                </FlexContainer>
            </section>
            <section className={styles.section}>
                <Text level="h2" className={styles.title}>如何使用</Text>
                <Text>样式变量如下：</Text>
               <Code type="less" code={lessStyle}></Code>
            </section>
        </div>
    )
}


export default ColorView;
