import React, {
    CSSProperties,
    ForwardedRef,
    forwardRef,
    FunctionComponentElement, ReactElement,
} from 'react';
import classnames from 'classnames';
import styles from './index.less';

type Element=ReactElement|string|undefined|null;

export interface Props{
    flex:'vertical-center'|'horizontal-center'|'center'|'vertical-full'|'horizontal-full'|'horizontal-warp',
    className?:string,
    children:Element[]|Element,
    onClick?:React.MouseEventHandler,
    style?:CSSProperties
    title?:string,
}

const FlexContainer = (props:Props, ref:ForwardedRef<HTMLDivElement>|null):FunctionComponentElement<Props> => {
    const {
        flex, children, className, onClick, style, title,
    } = props;

    return (
        <div
            className={classnames({
                [styles.vCenter]: flex === 'vertical-center',
                [styles.hCenter]: flex === 'horizontal-center',
                [styles.center]: flex === 'center',
                [styles.vFull]: flex === 'vertical-full',
                [styles.hFull]: flex === 'horizontal-full',
                [styles.hWarp]: flex === 'horizontal-warp',
            }, className)}
            onClick={onClick}
            ref={ref}
            style={style}
            title={title}
        >
            {children}
        </div>
    );
};

export default forwardRef(FlexContainer);
