import React, {
    CSSProperties,
    ForwardedRef, forwardRef, FunctionComponentElement, ReactElement, useMemo,
} from 'react';
import classnames from 'classnames';
import styles from './index.less';

type Element=ReactElement|string|undefined|null;

export interface Props{
    className?:string,
    children?:Element[]|Element,
    grow?:boolean,
    basis?:number|string,
    scroll?:boolean,
    onClick?:React.MouseEventHandler,
    onScroll?:React.UIEventHandler<HTMLDivElement>,
    style?:CSSProperties,
}

const FlexItem = (props:Props, ref:ForwardedRef<HTMLDivElement>|null):FunctionComponentElement<Props> => {
    const {
        children, className, grow = false, basis, onClick, scroll, onScroll, style = {},
    } = props;

    const calculatedStyle = useMemo(() => {
        const result:CSSProperties = { ...style };
        if (basis) {
            result.flexBasis = basis;
        }
        return result;
    }, [style, basis]);

    return (
        <div
            className={classnames(styles.item, {
                [styles.grow]: grow,
                [styles.scroll]: scroll,
            }, className)}
            style={calculatedStyle}
            onClick={onClick}
            onScroll={onScroll}
            ref={ref}
        >
            {children}
        </div>
    );
};

export default forwardRef(FlexItem);
