import React, { FunctionComponentElement, useMemo } from 'react';
import classnames from 'classnames';
// import * as Prism from 'prismjs';
// import 'prismjs/components/prism-typescript';
// import 'prismjs/components/prism-less';
// import 'prismjs/components/prism-jsx';
// import 'prismjs/components/prism-tsx';
// import 'prismjs/components/prism-powershell';
// import 'prismjs/themes/prism.css';
// import 'prismjs/themes/prism-tomorrow.css';
import styles from './index.less';

export interface Props {
    code: string,
    type: 'ts' | 'powershell' | 'less' | 'tsx'
    className?: string,
}


const Code = (props: Props): FunctionComponentElement<Props> => {
    const { code, className, type = 'less' } = props;

    return (
        <pre className={classnames(className, styles.code, {
            'language-typescript': type === 'ts' || type === 'tsx',
            'language-less': type === 'less',
            'language-powershell': type === 'powershell',
        })}
        >
            <code
                className={classnames({
                    'language-typescript': type === 'ts' || type === 'tsx',
                    'language-less': type === 'less',
                    'language-powershell': type === 'powershell',
                })}
            // dangerouslySetInnerHTML={{ __html: code }}
            >
                {code}

            </code>
        </pre>
    );
};

export default Code;
