import React, { FunctionComponentElement } from 'react';
import classnames from 'classnames';
import styles from './index.less';

export interface Props{
    level?:'h1'|'h2'|'h3'|'importent'|'text'|'tips',
    ellipsis?:boolean,
    pre?:boolean,
    className?:string,
    children?:string,
    style?:any
}

const Text = (props:Props):FunctionComponentElement<Props> => {
    const {
        level = 'text', ellipsis, className, children, pre, style
    } = props;

    return (
        <div className={classnames(className, {
            [styles.h1]: level === 'h1',
            [styles.h2]: level === 'h2',
            [styles.h3]: level === 'h3',
            [styles.main]: level === 'importent',
            [styles.text]: level === 'text',
            [styles.tips]: level === 'tips',
            [styles.ellipsis]: ellipsis,
            [styles.pre]: pre,
        })}
        style={style}
        >
            {children}
        </div>
    );
};

export default Text;
